import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class LoaderService {
  visibleLoader = false;

  constructor() {}

  showLoader() {
    this.visibleLoader = true;
  }

  hideLoader() {
    this.visibleLoader = false;
  }

  toggleLoader() {
    this.visibleLoader = !this.visibleLoader;
  }
}
